.home {
  direction: ltr;
  height: 100vh;
  background: linear-gradient(to left, #3772a9, #0b2949);
  background-repeat: no-repeat;
  width: 100%;
  flex-grow: 1;
  .ahmedsPhoto {
    height: 100vh;
    width: 100vw;
    background: url("../../assets/image/Ahmed'sPhoto.png") left bottom no-repeat;
    position: absolute;
    background-size: 80vh;
    left: 0;
  }

  .bgLines {
    height: 100vh;
    width: 50%;
    background: url("../../assets/image/bg-lines.png") right bottom no-repeat;
    position: absolute;
    background-size: contain;
    right: 0;
    // bottom: 0;
  }
  .outline-bg {
    stroke: white;
    stroke-width: 5px;
    position: absolute;
    fill: transparent;
    width: 100%;
    height: 100vh;
  }
  .ahmedsDetails {
    display: flex;
    justify-content: center;
    position: absolute;
    right: 0;
  }
  .ahmeds-details-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    // position: relative;

    .association {
      color: #fff;
      font-size: 1.73vw;
      text-align: center;
    }

    .ahmeds-details {
      font-weight: bold;
      display: flex;
      // width: 100%;
      // justify-content: center;
      // align-items: center;
      .ahmeds-name {
        font-family: GE;
        font-size: 5vw;
        color: #fff;
        text-shadow: 0px 0.7vw 5px rgba(0, 0, 0, 0.363);
        .elasfar-name {
          font-family: GE;
          color: #fffc35;
        }
      }
    }
    h1 {
      margin: 0;
    }

    ._2021 {
      font-family: Poet;
      color: #fff;
      font-size: 4vw;
    }
    .show-users-button {
      position: relative;
      z-index: 2;
      margin-top: 30px;
      border: #fff solid 1px;
      color: #fff;
      background-color: transparent;
      font-size: 25px;
      border-radius: 20px;
      width: 250px;
      transition: 0.35s ease-in-out;
      &:hover {
        color: #015ec5;
        background-color: #ffffff;
      }
    }
  }
  .home-rectangle {
    // position: relative;
  }
  .signOut {
    width: 200px;
    font-family: GE;

    position: absolute;
    font-size: 25px;
    color: #fff;
    cursor: pointer;
    z-index: 1;
  }

  // .ahmeds-name {
  //       font-size: 5vw;
  //       color: #fff;
  //       text-shadow: 0px 0.7vw 5px rgba(0, 0, 0, 0.363);
  //       .elasfar-name {
  //         color: #fffc35;
  //       }
  //     }

  .home-logo {
    position: absolute;
    bottom: 10px;
    right: 80px;
  }
}
@media screen and (max-width: 599px) {
  .home {
    .bgLines {
    }
    .ahmedsDetails {
      .ahmeds-details-container {
        height: 50vh;

        .association {
          margin-top: 150px;
          color: #fff;
          font-size: 7.5vw;
          text-align: center;
        }
        .ahmeds-details {
          .ahmeds-name {
            font-size: 20vw;
            text-shadow: 0px 2.023vw 5px rgba(0, 0, 0, 0.363);
            // font-weight: bold;
            .elasfar-name {
            }
          }
        }
        ._2021 {
          font-size: 11.56vw;
          font-family: Poet;
        }
        .show-users-button {
        }
      }
    }
    .home-rectangle {
      display: none;
      .outline-bg {
      }
    }
    .signOut {
      width: 100%;
      text-align: center;
      font-size: 6.5vw;
    }
    .ahmedsPhoto {
      height: 100vh;
      // height: 85vh;
      width: 60vw;
      background: url("../../assets/image/Ahmed'sPhoto.png") left bottom
        no-repeat;
      position: absolute;
      // background-size: 85vh;
      background-size: contain;
      left: 0;
    }
    .home-logo {
      width: 40%;
      bottom: 10px;
      right: 10px;
    }
  }
}
